import React from 'react';
import PropTypes from 'prop-types';

import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Chip from '@material-ui/core/Chip';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Radio from '@material-ui/core/Radio';

import { Field } from 'formik';
import { TextField, RadioGroup } from 'formik-material-ui';

import { isRequired, calcularDiaSemana, validarPaso2 } from '../../helpers/checkoutHelpers';

import Flash from 'react-reveal/Flash';

import withStyles from '@material-ui/core/styles/withStyles';
const styles = theme => ({    
    buttons: {
        display: 'flex',
        margin: '0 auto',
        marginTop: theme.spacing.unit * 2,        
        marginBottom: theme.spacing.unit * 2,
        justifyContent: 'center',      
    },
    button: {                
        paddingLeft: theme.spacing.unit * 4,
        paddingRight: theme.spacing.unit * 4,
        paddingTop: theme.spacing.unit * 1.5,
        paddingBottom: theme.spacing.unit * 1.5,
        fontSize: 18
    },

    
    botonOpcion: {
        textTransform:'none', 
        marginBottom: theme.spacing.unit * 1.5, 
        padding:`${theme.spacing.unit * 1.5}px ${theme.spacing.unit * 2}px`
    },
    
    labelRetiro: {
        fontSize: 16,
        marginTop: theme.spacing.unit * 3,
        textAlign: 'justify',
    }
});

const FormEntrega = ({ classes, direcciones, validationStep, values, errors, 
handleNext, handleNextValidationStep, setFieldTouched, setFieldValue, handleBack, handleRemoverDireccion }) => {

    return (
        <Grid container >                                                                                                        
                                                
            <Grid item xs={12}  >
                {
                    (validationStep === 2) && errors.tipoEntrega ?
                        <Flash>
                            <Typography variant="subtitle2" align='center' style={{fontSize: 20, color: 'red'}}>
                                Elegí una forma de entrega
                            </Typography>
                        </Flash>
                    :
                        <Typography variant="subtitle2" align='center' style={{fontSize: 20}}>
                            Elegí una forma de entrega
                        </Typography>
                }
            </Grid>
            
            <Grid item xs={12}>

                <Field name="tipoEntrega" validate={isRequired} component={RadioGroup}  >                                                                                                                                    
                    
                    <FormControlLabel 
                        value="retiro" control={<Radio color='primary'/>} 
                        label={`Retiro por el barrio de Colegiales (Santos Dumont y Cramer), a coordinar día y horario con el vendedor.`} 
                        classes={{ label: classes.labelRetiro}}
                    />
                    
                    <FormControlLabel 
                        value="envio" control={<Radio color='primary'/>} 
                        label="Envío a domicilio (únicamente en barrios Colegiales - Belgrano -  Palermo - Núñez -  Cañitas - Coghlan y Villa Urquiza)" 
                        classes={{ label: classes.labelRetiro}}
                    />

                    <Typography variant="subtitle2" style={{marginTop: 8, marginLeft: 32}}>
                        Costo de envío $100
                    </Typography>
                </Field>
            </Grid>

            { direcciones.length > 0 &&                                                        
                <Grid item container xs={12}>
                    <Grid item xs={12} sm={4}>
                        <Typography variant="caption" align='center'>
                            Direcciones utilizadas anteriormente:
                        </Typography>
                    </Grid>
                    <Grid item container xs={12} sm={8}>
                        {
                            direcciones.map((direccion, index) => (
                                <Grid item key={index}>                                                                            
                                    <Chip                                                                                                                        
                                        label={direccion}
                                        onClick={() => setFieldValue('entregaDireccion', direccion)}
                                        onDelete={() => handleRemoverDireccion(direccion)} 
                                        style={{margin: '0 4px'}}  
                                        color={direccion === values.entregaDireccion ? 'primary' : 'default'} 
                                    />
                                </Grid>
                            ))
                        }
                    </Grid>

                </Grid>                                                        
            }
            
            
            { values.tipoEntrega === 'envio' && 

                <React.Fragment>
                    <Field validate={isRequired} name="entregaDia" component={RadioGroup}>
                        <FormControlLabel
                            value='1'
                            control=
                            {
                                <Radio                                                                                    
                                    icon={ 
                                        <Button 
                                            className={classes.botonOpcion}
                                            style={{                                                                                        
                                                color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                            }} 
                                        variant="outlined">Lunes 1 de 11 a 16hs</Button>                                                                                
                                    }
                                    checkedIcon={                                                                                
                                        <Button className={classes.botonOpcion} variant="contained" color='primary'>Lunes 1 de 11 a 16hs</Button>                                                                                
                                    }                                                                            
                                />
                            }
                        />
                        <FormControlLabel
                            value='2'
                            control=
                            {
                                <Radio                                                                                    
                                    icon={ 
                                        <Button 
                                            className={classes.botonOpcion}
                                            style={{                                                                                        
                                                color: (validationStep === 2) && errors.entregaDia ? 'red' :'',
                                                borderColor: (validationStep === 2) && errors.entregaDia ? 'red' :''
                                            }}
                                        variant="outlined">{calcularDiaSemana(1)}</Button>                                                                                
                                    }
                                    checkedIcon={                                                                                
                                        <Button className={classes.botonOpcion} variant="contained" color='primary'>{calcularDiaSemana(1)}</Button>                                                                                
                                    }                                                                            
                                />
                            }
                        />                                                                        
                    </Field>
                    
                    <Grid item xs={10} style={{margin: '0 auto'}}>
                        <Field 
                            onKeyPress={ 
                                (e) => {
                                    if (e.key === 'Enter') {                                                                            
                                        validarPaso2(values, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)
                                    }
                                }
                            }
                            variant='filled' fullWidth type="text" component={TextField} name="entregaDireccion" 
                            validate={isRequired} label="Dirección de entrega"
                        />
                    </Grid>
                </React.Fragment>

                
            }
            

            <div className={classes.buttons} style={{marginTop: 24}}>                                                                                        
                <Button size='small' onClick={() => handleBack()} className={classes.button} style={{fontSize: 12}}>
                    Atrás
                </Button>                                                
                <Button
                    size='large'
                    variant="contained"
                    color="primary"
                    onClick={() => validarPaso2(values, validationStep, errors, handleNext, handleNextValidationStep, setFieldTouched)}                                            
                    className={classes.button}
                >
                    Continuar
                </Button>
            </div>                                        
        </Grid>
    );
};

FormEntrega.propTypes = {
    classes: PropTypes.object.isRequired,
    direcciones: PropTypes.array.isRequired,
    validationStep: PropTypes.number.isRequired,
    values: PropTypes.object.isRequired,
    errors: PropTypes.object.isRequired,
    handleNext: PropTypes.func.isRequired, 
    handleNextValidationStep: PropTypes.func.isRequired, 
    setFieldTouched: PropTypes.func.isRequired, 
    setFieldValue: PropTypes.func.isRequired, 
    handleBack: PropTypes.func.isRequired, 
    handleRemoverDireccion: PropTypes.func.isRequired,
};

export default withStyles(styles)(FormEntrega);